export default {
	props:{
		swipers:Array,
		height:{
			type:String,
			default:"350rpx"
		},
		preview:{
			type:Boolean,
			default:false
		}
	},
	computed:{
		getStyle(){
			return `height: ${this.height}rpx;`
		},
		getUrls(){
			return this.swipers.map((v,k) => v.src)
		}
	},
	methods:{
		event(item,index){
			if(this.preview){
				return uni.previewImage({
					current:index,
					urls:this.getUrls
				})
			}
		}
	}
}
